body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;

	background-repeat: repeat-y;
	background-size: contain;
	overflow: auto;
	font-family: 'Roboto', sans-serif;
}

.App {
	text-align: center;
	overflow: hidden;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

.inputForm-main-div {
	margin: auto;
	max-width: 500px;
	margin-top: 20px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

.input-form {
	margin: auto;
	padding: 20px;
	height: 555px;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.button-div {
	display: flex;
	flex-direction: row;
}
.title {
	margin: 0;
	padding: 0;
	text-align: left;
	max-width: 500px;
	margin: auto;
	padding-left: 20px;
}

.checkbox-div {
	display: flex;
	flex-direction: column;
	justify-content: safe;
}

.checkbox-title {
	text-align: left;
	color: #4e4e4e;
	font-size: 1.1em;
	margin-bottom: 15px;
}

.reminderFor {
	border: 1px solid red;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

.logout-button {
	margin-right: 20px;
	width: 24px;
	height: 24px;
	background-color: transparent;
	border-style: none;
}

.logout-button:focus {
	outline: none;
}

.login-main-div {
	margin: auto;
	max-width: 500px;
	margin-top: 100px;
}

.input-form-login {
	margin: auto;
	padding: 20px;
	height: 220px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.title-div {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.error {
	padding: 0;
	margin: 0;
	color: red;
	padding-right: 20px;
}
.title-login {
	padding-left: 20px;
	font-size: 1.5em;
	font-weight: 600;
}

.MuiButton-label {
	height: 25px;
}

